.sidebar{
    color: white;
    height: 100vh;
    width: 100px;
    min-width: 230px;
    flex: 0.2;
    background-color: #040404;
    padding: 0 10px;
}


.sidebar__logo{
    object-fit: contain;
    width: 130px;
    padding: 20px;
}

.sidebar__title{
    margin-left: 20px;
    padding: 5px;
    font-size: 12px;
}

.sidebar > hr{
    border: 1px solid #282828;
    width: 90%;
    margin: 10px;
}