.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    min-width: 300px;
}

.header__left{
    flex: 0.5;
    background-color: white;
    color: gray;
    border-radius:30px;
    display: flex;
    align-items: center;
    display: flex;
    align-items: center;
    padding: 10px;
}

.header__left > input {
    border: none;
    width: 100%;
    
}
.header__left > input:focus{
    outline: none;
}

.header__right {
    display: flex;
    align-items: center;
}

.header__right > h4{
    margin-left: 10px;
}