.sidebarOption{
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color ease-in;
    display: flex;
    align-items: center;
}


.sidebarOption:hover{
    color: white;
    border-left: 4px solid #1db954;
}

.sidebarOption__icon{
    margin: 0 10px;
}

.sidebarOption > p{
    margin: 10px 0 0 20px;
    font-size: 14px;
}